.modal-note-list-main-container{
    position: absolute;
    background-color: rgba(36, 36, 36, .5);
    backdrop-filter: blur(8px);
    top: 6vh;
    
   
   
    height: 92vh;
    max-height: 92vh;
    z-index: 50;
    width: 100vw;
    
}



.modal-note-list-fix-container{
   
    width: 100%;
    height: 90vh;
    max-height: 90vh;
    
    
    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    color: var(--color-f);
    overflow: scroll;
    overflow-x: hidden;
    
    
   
}
.modal-note-list-fix-container::-webkit-scrollbar { 
    width:0 !important;
    display:none;
 }




.modal-note-card-main-container{
  width: 40%;
  height:auto;
  max-height: 34vh;
  
  
 
  border-radius: 12px;
  margin-right: .8em;
  margin-left: .8em;
  background-color: aliceblue;
  transition: .1s ease-out;
  margin-top: 2em;
  
 margin-bottom: 2em;
   
  
}


.modal-note-card-main-container:nth-child(1) {
    margin-top: 4em;
    margin-bottom: 4em;
}

.modal-note-card-main-container:nth-child(2) {
    margin-top: 8em;
  
}
.modal-note-card-main-container:last-child{
    margin-bottom: 4em;
    margin-top: 2em;
    
}



.modal-note-card-main-container:hover{
    transform: scale(1.1);
    

}
.modal-note-list-card-container{
    width: 100%;
    flex-wrap: wrap;
    
    
    
    
}
.modal-note-card-edit-icon{
    background-color: var(--color-a);
    filter:brightness(1.4);
    border-radius: 6px;
    padding: .3em;
    transition: .2s ease-out;

}
.modal-note-card-edit-icon:active{
    transform: scale(.05);
    transform: rotate(101deg);
    
    
}
.modal-note-card-edit-icon2{
    background-color: var(--color-b);
    border-radius: 6px;
    padding: .3em;
    transition: .2s ease-out;
}
.modal-note-card-edit-icon2:active{
    transform: scale(.05);
    transform: rotate(120deg);
}

.modal-note-icon-container{
    align-self: flex-end;
    padding-top: .2em;
    padding-bottom: .1em;
   margin-right: .3em;
   margin-left: .3em;
}


.modal-note-card-margin-top{
    margin-top: -4em;
}


.modal-note-card-title-container{
    
    display: flex;
    background-color: var(--color-a);
    width: 100%;
    border-radius: 10px;
    margin-top: -2px;
    height: 60px;
    
    text-align: center;
    border: 4px solid var(--color-a);
    

}

.modal-note-card-title-container p{
    
  
   max-width: 100%;
  
   word-break: break-all;
}

.modal-note-card-body-container{
    width: 90%;
    overflow: scroll;
    overflow-x: hidden;
    height: 80%;
    white-space:pre-wrap;
    font-size: 16px;
   
   
  
}


.modal-note-card-body-container::-webkit-scrollbar{
    display: none;
}

.modal-note-card-body-container p{
    word-break: break-all;
 
  
}
