    .login-modal-main-container{
        position: absolute;
        z-index: 30;
        width: 100vw;
        height: 100vh;
        background-color: rgba(36, 36, 36, .4);
        backdrop-filter: blur(4px);
    }

    .loginButton{
        width: 50%;
        position: absolute;
        top: 0;
        height: 60px;
        bottom: 0;
        border: 2px solid var(--color-a);
        border-radius: 16px;
        color: var(--color-a);
        right: 0;
        left: 0;
        margin: auto;
    }
    .login-modal-form{
        width: 60%;
        padding: 2em;
        border-radius: 16px;
        background-color: var(--color-f);
        
    }

    .login-modal-container{
        width: 100%;
        height:auto;
    }

    label p{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }


    input[type=email]{
        padding: .5em;
        border: 2px solid var(--color-b);
        border-radius: 10px;
        transition: .2s ease-in;
        
        
        
    }

    input[type=email]:focus{
        outline: none;
        padding-top: 1.4em;
        padding-bottom: 1.4em;
        border: 2px solid var(--color-a);
        
        
        
    }
    input[type=password]{
        padding: .5em;
        border: 2px solid var(--color-b);
        border-radius: 10px;
        transition: .2s ease-in;
        
    }
    input[type=password]:focus{
        outline: none;
        padding-top: 1.4em;
        padding-bottom: 1.4em;
        border: 2px solid var(--color-a);
        
        
        
    }

    input[type=name]{
        padding: .5em;
        border: 2px solid var(--color-b);
        border-radius: 10px;
        transition: .2s ease-in;
        
    }
    input[type=name]:focus{
        outline: none;
        padding-top: 1.4em;
        padding-bottom: 1.4em;
        border: 2px solid var(--color-a);
        
        
        
    }

    input[type=submit]{
        width: 120px;
        padding: .5em;
        border: none;
        background-color: var(--color-a);
        color: #fff;
        font-size: 16px;
        font-weight: 500;
        border-radius: 10px;
    }



    .login-modal-create-account-p{
        text-decoration: underline;
    }

    .form-create-p{
        margin-bottom: 0px;
    }


    .create-user-form-submit-button{
        padding-top: .5em;
        padding-bottom: .5em;
        border-radius: 10px;
        color: var(--color-f);
        background-color: var(--color-a);
        border: none;
        padding-left: 1em;
        padding-right: 1em;
        font-weight: 500;
        transition: .2s ease-out;
    }
    .create-user-form-submit-button:active{
        transform: scale(1.1);
        background-color: var(--color-f);
        color: var(--color-a);
        border: 2px solid var(--color-a);
    }

    .cloudinary-button{
        width: auto;
        padding-top: .2em;
        padding-bottom: .2em;
        border-radius: 10px;
        background-color: var(--color-b);
        margin-top: 16px;
        text-align: center;
        transition: .2s ease-out;
        box-shadow:  none !important;
    }
    .cloudinary-button img{
        width: 18px;
    }

    .cloudinary-button:active{
        transform: scale(1.1);
        background-color: var(--color-f);
        color: var(--color-b);
        border: 2px solid var(--color-b);

    }
 
    .cloudinary-button-uploaded{
        background-color: var(--color-c);
    }